/**
 * Écran des propriétés d'un atelier
 */

export interface OWspPropsInit<E> extends OSkinableInit {
	reg: IReg<E>
	checkDirtyOnClose?: boolean
	disableRevert?: boolean
	exportScwsp?: boolean
}

abstract class WspProps<E extends IWspUiEnv | IChainEnv> extends BaseAreaViewAsync<E> {
	public reg: IReg<E>;
	protected config: OWspPropsInit<E>;

	/**
	 * Lists extpoints des areas de la fenêtre
	 */
	abstract get areaList(): string[];

	protected _form: HTMLFormElement;
	protected _msg: MsgLabel;
	protected _emptyData: Dict<any> = {};
	protected _lastWspData: Jsonisable;
	protected _fixedWspData: Jsonisable = {};

	protected _revertBtn: Button;
	protected _saveBtn: Button;
	protected _closeBtn: Button;
	protected _exportBtn: Button;

	get wsp(): Wsp | null {return (this.reg.env as IWspUiEnv).wsp || null};

	protected async _initialize(init: OWspPropsInit<E>): Promise<void> {
		this.config = Object.create(init);// L'objet init est mutable et mémorisé => clone sans modifier l'objet initial
		this.reg = REG.createSubReg(init.reg);
		if (this.wsp) {
			(this.reg.env as IWspUiEnv).place = this.wsp.newPlaceWsp();
			(this.reg.env as IWspUiEnv).place.eventsMgr.on("wspLiveStateChange", this.onWspLiveStateChange.bind(this));
			(this.reg.env as IWspUiEnv).place.eventsMgr.on("wspLiveFullLoadStateChange", this.onWspLiveStateChange.bind(this));
		}

		if (!this.config.disableRevert) this._revertBtn = <Button î={{disabled: true, label: "Rétablir", uiContext: "dialog"} as OButtonInit} onclick={this.onRevert}/> as Button;
		this._saveBtn = <Button î={{disabled: true, label: "Enregistrer", uiContext: "dialog"} as OButtonInit} class="default" onclick={this.onSave}/> as Button;
		this._closeBtn = <Button î={{disabled: false, label: "Annuler", uiContext: "dialog"} as OButtonInit} onclick={this.onClose}/> as Button;
		if(init.exportScwsp && this.reg.hasPerm("read.node.export")) this._exportBtn = <Button î={{label: "Exporter une archive de l'atelier", uiContext: "dialog"} as OButtonInit} onclick={this.onExport}/> as Button;

		this._form = <form id="form"/> as HTMLFormElement;
		this._msg = <MsgLabel/> as MsgLabel;

		const sr = this.attachShadow(DOMSH.SHADOWDOM_INIT);
		this.reg.installSkin("standard-dialog", sr);
		this.reg.installSkin("form-control-areas", sr);
		this.reg.installSkin("c-wsp-props", sr);
		this._initAndInstallSkin(this.localName, init);
		DOM.append(sr, this._msg,
			this._form,
			<div id="footer">
				{this._exportBtn ? [
					this._exportBtn,
					<div style="flex:1000"/>
				] : undefined}
				{this._revertBtn}
				{this._saveBtn}
				{this._closeBtn}
			</div>
		);

		let checkDirtyRequest = 0;
		const checkBtnsState = () => {
			if (checkDirtyRequest) {
				clearTimeout(checkDirtyRequest);
				checkDirtyRequest = 0;
			}
			checkDirtyRequest = setTimeout(this.refreshBtnsState.bind(this), 300) as any;
		};
		this._form.addEventListener('input', checkBtnsState);
		this._form.addEventListener('change', checkBtnsState);

		this.addEventListener('keydown', (ev) => {
			if (ev.key === 'Enter' && ACTION.isAccelPressed(ev)) {
				ev.preventDefault();
				ev.stopImmediatePropagation();
				this._saveBtn.click();
				return true;
			}
			return false;
		});

	}

	protected async initForm(form: HTMLFormElement): Promise<void> {
		this.initFormELt(form);
		await AREAS.applyLayout(this._form, this.reg.mergeLists<ControlArea>(...this.areaList), this.areasContext(), true);
		this._emptyData = FORMS.formToJson(this._form);
	}

	protected initFormELt(form: HTMLFormElement): void {
		form.appendChild(
			<fieldset class="fieldslist">
				<div class="fields" area-ids="*"/>
			</fieldset>);
	}

	onViewShown(): void {
		if (this.wsp) {
			this._msg.setStandardMsg("loading");
			// ATTENTION : l'init du form est dépendante du reg enrichi par wspMetaUi. Le load de wspMetaUi est donc nécessaire
			this.wsp.waitForWspMetaUiAvailable().then(async (wsp) => {
				await this.initForm(this._form);
				await this.updateForm(false);
				this._msg.setStandardMsg(null);
			}).catch(async () => {
				await this.initForm(this._form);
				await this.updateForm(false);
				this._msg.setStandardMsg(null);//statut déjà affiché par ailleurs
				//this._msg.setCustomMsg("Chargement de l'atelier partiel", "error");
			});
		} else
			this.initForm(this._form).then(async () => {
				await this.updateForm(false);
			});
	}

	/**
	 * Contexte des areas
	 */
	protected areasContext(): IControlAreaContext & IRegPointer<E> {
		return {
			reg: this.reg,
			buildControlLabel: true
		};
	}

	/**
	 * Construit la liste des propriétés à exploiter (fournies par le FORM et les fixed)
	 */
	protected async getParamsToSave(): Promise<Jsonisable> {
		return Object.assign(FORMS.formToJson(this._form), this._fixedWspData);
	}

	/**
	 * Construit la liste des propriétés courantes du wsp (ie initiales : non modifiées par le form)
	 */
	protected async getIniParams(): Promise<Jsonisable> {
		return this.wsp ? this.wsp.makeParamsForUpdate() : this._emptyData;
	}

	/**
	 * Action de validation
	 * @param ev
	 */
	protected _onSaveShowWaitMsg: boolean = true;

	protected async onSave(this: Button, ev: Event): Promise<void> {
		if (!this.disabled) {
			const me = DOMSH.findHost<WspProps<E>>(this);
			if (me._form.reportValidity()) {
				me._listenLiveStateChange = false;
				try {
					this.disabled = true;
					if (me._onSaveShowWaitMsg) {
						await new MsgOver().setCustomMsg("Veuillez patienter...", "info").showMsgOver(me).waitFor(
							me._doExecuteValid()
						);
					} else
						await me._doExecuteValid();
				} finally {
					me._listenLiveStateChange = true;
					// Désactivation du listner d'update auto de l'interface le temps du save async (via _listenLiveStateChange)
					// => update explicite utile
					me.onWspLiveStateChange(me.wsp);
				}
			} else
				POPUP.showNotifWarning("Veuillez corriger le formulaire.", this);
		}
	}

	/**
	 * Action de validation (sans contrôle déjà réalisé)
	 * @param ev
	 */
	abstract _doExecuteValid(): Promise<void>;

	/**
	 * Action de fermeture / abandon
	 * @param ev
	 */
	protected async onClose(this: Button, ev: Event): Promise<void> {
		const me = DOMSH.findHost<WspProps<E>>(this);
		if (!me.config.checkDirtyOnClose || !await me.isDirty() || await POPUP.confirm("Attention, vos modifications n'ont pas été enregistrées.", me, {cancelLbl: "Annuler", okLbl: "Quitter sans enregistrer"})) {
			return me._doClose();
		}
	}

	/**
	 * Action de la fermeture
	 * @param ev
	 */
	async _doClose(returnValue?: any): Promise<void> {
		POPUP.findPopupableParent(this).close(returnValue);
	}

	/**
	 * Action de revert des modifications
	 * @param ev
	 */
	protected async onRevert(this: Button, ev: Event): Promise<void> {
		if (!this.disabled) {
			const me = DOMSH.findHost<WspProps<E>>(this);
			return me._doRevert();
		}
	}

	/**
	 * Action de revert
	 * @param ev
	 */
	async _doRevert(): Promise<void> {
		return this.updateForm(true);
	}

	protected onExport(this: Button, ev: Event) {
		const me = DOMSH.findHost<WspProps<E>>(this);
		new ExportWspAction().execute(me);
	}

	/**
	 * Retourne true si au moins une valeur du formulaire a changé
	 */
	protected async isDirty(): Promise<boolean> {
		const wspData = FORMS.formToJson(this._form);
		/*console.log("DIRTY :: ", JSON.stringify(wspData) !== JSON.stringify(this._lastWspData));
		console.log("LAST :: ", JSON.stringify(this._lastWspData));
		console.log("NEW  :: ", JSON.stringify(wspData));*/
		return JSON.stringify(wspData) !== JSON.stringify(this._lastWspData);
	}

	/**
	 * Rafraichissement des boutons
	 */
	protected async refreshBtnsState(isPendingState?:true|undefined): Promise<void> {
		const dirty = await this.isDirty();
		this._closeBtn.label = "Fermer";
		if (this._revertBtn) this._revertBtn.disabled = isPendingState || !dirty;
		if (this._exportBtn) this._exportBtn.disabled = isPendingState;
		this._form.checkValidity()
		this._saveBtn.disabled = isPendingState || !dirty;
	}

	private _pendingUpdtFormCycle: number;
	protected async updateForm(clear: boolean = true): Promise<void> {
		const currentUpdtFormCycle = this._pendingUpdtFormCycle = Date.now();
		this._msg.setStandardMsg("loading");
		DOM.setHidden(this._form, true);
		try {
			await this.refreshBtnsState(true);
			if (currentUpdtFormCycle != this._pendingUpdtFormCycle) return;//raceCond
			await this.wsp?.waitForLoad();
			if (currentUpdtFormCycle != this._pendingUpdtFormCycle) return;//raceCond
			if (this.wsp?.migrating) {
				this._msg.setCustomMsg("Migration de l'atelier en cours...");
				await this.wsp.migrating;
				if (currentUpdtFormCycle != this._pendingUpdtFormCycle) return;//raceCond
			} else if (this.wsp?.fullLoading) {
				this._msg.setCustomMsg("Chargement de l'atelier en cours...");
				await this.wsp.fullLoading;
				if (currentUpdtFormCycle != this._pendingUpdtFormCycle) return;//raceCond
			}
			if (clear) FORMS.jsonToForm(this._emptyData, this._form);
			FORMS.jsonToForm(await this.getIniParams(), this._form, true, true);
			await FORMS.checkAsyncValidity(this._form);
			if (currentUpdtFormCycle != this._pendingUpdtFormCycle) return;//raceCond
			this._lastWspData = FORMS.formToJson(this._form);
			await this.refreshBtnsState();
			if (currentUpdtFormCycle != this._pendingUpdtFormCycle) return;//raceCond
			this._msg.setStandardMsg(null);
			this._form.checkValidity();
			DOM.setHidden(this._form, false);
		} catch (e) {
			this._msg.setCustomMsg("Chargement des propriétés de l'atelier en erreur", "error");
			return ERROR.log(e);
		}
	}

	onViewHidden(closed?: boolean): void {
		if (closed && this.wsp)	(this.reg.env as IWspUiEnv).place.closePlace();
	}

	onViewBeforeHide(close?: boolean): boolean {
		return false;
	}

	async onViewWaitForHide(close?: boolean): Promise<boolean> {
		if (!this.config.checkDirtyOnClose || !await this.isDirty() || await POPUP.confirm("Attention, vos modifications n'ont pas été enregistrées.", this, {cancelLbl: "Annuler", okLbl: "Quitter sans enregistrer"})) {
			await this._doRevert();
			return true;
		}
		return false;
	}

	_listenLiveStateChange: boolean = true;

	async onWspLiveStateChange(wsp: Wsp) {
		if (wsp !== this.wsp || !wsp) return;
		if (wsp.isDeleted === true) {
			POPUP.showNotif("L'atelier a été supprimé", POPUP.findPopupableParent(this), {autoHide: 3000});
			return this._doClose();
		} else {
			if (this._listenLiveStateChange)
				return this.updateForm(false);
		}
	}
}

