/**
 * Création d'un atelier
 * @return sur le close : Wsp créé ou null
 */
export class WspCreateProps extends WspProps<IChainEnv> {
	protected async _initialize(init: OWspPropsInit<IChainEnv>) {
		init.disableRevert = true;
		await super._initialize(init);
		this._saveBtn.label = "Créer";
	}

	get areaListBase(): string[] {
		//
		let list: string[] = ["wspProps:create:commons"];
		switch (this.reg.env.universe.config.backEnd) {
		case "fs":
			list.push("wspProps:create:fs");
			break;
		case "odb":
			list.push("wspProps:create:db", "wspProps:create:db:other");
			break;
		}
		return list;
	}

	get areaList(): string[] {
		let list = this.areaListBase;
		list.push("wspProps:create:fromScratch");
		return list;
	}

	protected async getIniParams(): Promise<Jsonisable> {
		let datas = await super.getIniParams();
		/// Propriétés jamais récupérées dans le cas de créationn d'un atelier à partir d'un autre
		delete datas.title;
		delete datas.alias;
		delete datas.code;
		return datas;
	}

	private _existingWspmetaOnPathFs: JWspTypeInst | null;

	protected async refreshBtnsState(): Promise<void> {
		if (this.reg.env.universe.config.backEnd === "fs") {
			let wspTypeDiv = this._form.querySelector("*[area-id='wspType']") as HTMLDivElement;
			let wspTypeSelector = this._form.querySelector("wsptype-selector") as WspTypeSelector;
			// On vérifie l'existence de contenus à l'emplacement
			const params = await this.getParamsToSave();
			this._existingWspmetaOnPathFs = params.code ? await WSP.fetchExistWspFsOnPath(this.reg.env.universe.wspServer, params.code, params.folderContent) : null;
			this._saveBtn.label = this._existingWspmetaOnPathFs ? "Créer (récupérer l'atelier existant)" : "Créer";
			if (wspTypeDiv) DOM.setHidden(wspTypeDiv, this._existingWspmetaOnPathFs != null);
			if (wspTypeSelector) wspTypeSelector.disabled = this._existingWspmetaOnPathFs != null;
		}
		return super.refreshBtnsState();
	}

	async _doExecuteValid(): Promise<void> {
		try {
			const params = await this.getParamsToSave();
			let wspTypeInst;
			if (this.reg.env.universe.config.backEnd === "fs" && this._existingWspmetaOnPathFs) {
				// mode FS ET atelier existant => on récupère le wspMeta
				wspTypeInst = this._existingWspmetaOnPathFs;
			} else {
				let wspTypeSelector: WspTypeSelector = this._form.querySelector("wsptype-selector") as WspTypeSelector;
				wspTypeInst = wspTypeSelector.fillWspTypeInst();
			}
			const resp = await WSP.createWsp(this.reg.env.universe.wspServer, params, wspTypeInst);
			if (!resp)
				throw "no resp";
			return this._doClose(resp);
		} catch (e) {
			await ERROR.report("Création de l'atelier impossible.", e);
			await this.refreshBtnsState();
		}
	}

}

customElements.define('c-wsp-create-props', WspCreateProps);

/**
 * Création d'un atelier à partir d'une archive
 * @return sur le close : Wsp créé ou null
 */
export class WspCreateFromScwspProps extends WspCreateProps {

	get areaList(): string[] {
		let list = this.areaListBase;
		list.push("wspProps:create:fromScwsp");
		return list;
	}

	async _doExecuteValid(): Promise<void> {
		try {
			const params = await this.getParamsToSave();
			//scwspFile
			let wspTypeSelector: WspTypeSelector = this._form.querySelector("wsptype-selector") as WspTypeSelector;
			let scWspInput = this._form.elements.namedItem("scwspFile") as HTMLInputElement;
			if (!scWspInput.files || scWspInput.files.length == 0) throw "No scwsp file selected";
			const resp = await WSP.importWsp(this.reg.env.universe.wspServer, params, scWspInput.files.item(0));
			if (!resp)
				throw "no resp";
			this._doClose(resp);
		} catch (e) {
			await ERROR.report("Import de l'atelier impossible.", e);
			await this.refreshBtnsState();
		}
	}

}

customElements.define('c-wsp-create-fromscwsp-props', WspCreateFromScwspProps);

