/**
 * Modification / affichage des propriétés d'un atelier
 */

export interface OWspEditInit extends OSkinableInit {
	reg: IReg<IWspUiEnv>
	tabAreasLists?: string
}

export class WspEditProps extends BaseAreaViewAsync<IReg<IWspUiEnv>> implements IViewsContainer {
	reg: IReg<IWspUiEnv>
	_tabs: Tabs;
	_msg: MsgLabel;

	protected async _initialize(init: OWspEditInit) {
		this.reg = init.reg;
		this._msg = <MsgLabel/> as MsgLabel;
		this.reg.addToList("wsp-edit-tab", "infoArea", 1, new AreaAsync<OWspPropsInit<IWspUiEnv>, WspProps<IWspUiEnv>>()
			.setLabel("Propriétés")
			.setBodyBuilder((ctx) => {
				return <WspEditProps_info î={ctx} code="props"/> as WspEditProps_info;
			}), 1);
		this.reg.addToList("wsp-edit-tab", "advancedArea", 1, new AreaAsync<OWspPropsInit<IWspUiEnv>, WspProps<IWspUiEnv>>()
			.setLabel("Avancé")
			.setBodyBuilder((ctx) => {
				return <WspEditProps_advanced î={ctx} code="advanced"/> as WspEditProps_advanced;
			}), 101);

		// TODO : onglets "Permissions"

		// Contexte des actions : this
		this._tabs = <Tabs areas-context="wsp-edit-tab-ctxt" areas-list={init.tabAreasLists || "wsp-edit-tab"}/> as Tabs;
		this.reg.registerSvc("wsp-edit-tab-ctxt", 1, {
			reg: this.reg
		} as OWspPropsInit<IWspUiEnv>);

		this._attach(this.tagName, init, this._tabs);
		this.reg.installSkin("webzone:panel", this.shadowRoot);
	}

	onViewShown(): void {
		this._msg.setStandardMsg("loading");
		this.reg.env.wsp.waitForLoad().then(async () => {
			if (this.reg.env.wsp.backEnd === "odb" && await WSP.checkWspSvc(this.reg.env.wsp, this, "skinSet")) {
				this._tabs.addTab(new AreaAsync<OWspPropsInit<IWspUiEnv>, WspProps<IWspUiEnv>>()
					.setLabel("Habillages graphiques")
					.setDescription("Habillages graphiques des générateurs du modèle documentaire")
					.setBodyBuilder((ctx) => {
						return <WspEditProps_skins î={ctx} code="props"/> as WspEditProps_skins;
					}), {position: 1});
			}
			this._msg.setStandardMsg(null);
		})
	}

	onViewBeforeHide(close?: boolean): boolean {
		return false;
	}

	async onViewWaitForHide(close?: boolean): Promise<boolean> {
		return this._tabs.selectedTab.view.onViewWaitForHide(close);
	}

	visitViews(visitor: (view: IView) => any, options?: OViewVisitOptions): any {
		if (this._tabs) this._tabs.visitViews(visitor, options);
	}

	visitViewsAsync(visitor: (view: IView) => Promise<any>, options?: OViewVisitOptions): Promise<any> {
		if (this._tabs)
			return this._tabs.visitViewsAsync(visitor, options);
	}

}

