class WspEditProps_info extends WspProps<IWspUiEnv> {
	protected async _initialize(init: OWspPropsInit<IWspUiEnv>) {
		init.checkDirtyOnClose = true;
		init.exportScwsp = true;
		return super._initialize(init);
	}

	protected initFormELt(form: HTMLFormElement): void {
		form.appendChild(
			<fieldset class="fieldslist">
				<div class="fields" area-ids="*"/>
			</fieldset>);
		form.appendChild(
			<fieldset class="fieldslist" id="wspTypeFs">
				<div class="fields" area-ids="wspType"/>
			</fieldset>);
	}

	get areaList(): string[] {
		const wspDefProps = this.wsp.wspDefProps;
		let list: string[] = ["wspProps:edit:commons:infos"];
		switch (this.wsp.backEnd) {
		case "fs":
			list.push("wspProps:edit:fs:infos");
			break;
		case "odb":
			list.push("wspProps:edit:db:infos");
			break;
		}
		if (wspDefProps && wspDefProps.drfRefWsp)
			list.push("wspProps:edit:db:drf:infos");
		else if (wspDefProps && wspDefProps.drvAxis)
			list.push("wspProps:edit:db:drv:infos");
		else if (this.wsp.backEnd == "odb")
			list.push("wspProps:edit:db:other:infos");
		return list;
	}

	async _doExecuteValid(): Promise<void> {
		try {
			const allFormsDatas = await this.getParamsToSave();
			// Filtre newParams au juste nécessaire (params modifiés)
			let newParams: Jsonisable = {};
			let hasNewParams = false;
			for (const props in allFormsDatas) {
				if (props != "wspType")
					if (this._lastWspData[props] !== allFormsDatas[props]) {
						hasNewParams = true;
						newParams[props] = allFormsDatas[props];
					}
			}
			if (hasNewParams) await this.wsp.updateWspProps(newParams);
			if (JSON.stringify(allFormsDatas.wspType) != JSON.stringify(this._lastWspData.wspType)) {
				let isMigrationNeeded = await this.wsp.isMigrationNeeded(allFormsDatas.wspType);
				if (isMigrationNeeded === false) {
					await this.wsp.updateWspType(allFormsDatas.wspType, false);
					//return this.onWspLiveStateChange(this.wsp); NON : l'interface est actualisée ave cles remontées WS
				} else if (isMigrationNeeded === true) {
					const result = await POPUP.confirmCheck(<div>
							<div>Les contenus de votre atelier doivent être mis à jour.</div>
							<div><b>Vous ne pourrez pas annuler cette action.</b></div>
							<div>Voulez-vous poursuivre l'enregistrement du modèle documentaire ?</div>
						</div>,
						this, "Exécuter la mise à jour des contenus", true, {okLbl: "Poursuivre", cancelLbl: "Annuler"});
					if (result === false) await this._doRevert();
					else await this.wsp.updateWspType(allFormsDatas.wspType, result === "checked");
					//return this.onWspLiveStateChange(this.wsp); NON : l'interface est actualisée avec les remontées WS
				} else {
					let msg = isMigrationNeeded.getAttribute("desc");
					POPUP.showNotifError(`Une mise à jour automatique des contenus de l'atelier serait nécessaire, mais des modifications manuelles préalables doivent être réalisées : ${msg}`, this, {noCloseBtn: true, kind: "warning",});
					await this._doRevert();
					//return this.onWspLiveStateChange(this.wsp); NON : l'interface est actualisée avec les remontées WS
				}
			}
		} catch (e) {
			await ERROR.report("Modification des propriétés de l'atelier impossible.", e);
			await this.refreshBtnsState();
		}
	}

	protected isReadOnlyFieldset(fieldsetElt: HTMLFieldSetElement): boolean {
		if (fieldsetElt.id === "wspTypeFs")
			return !this.reg.hasPerm("action.wspProps#update.wsptype");
		else
			return !this.reg.hasPerm("action.wspProps#update.props");
	}

}

