/**
 * Suppression d'un atelier
 */
export class WspRemoveProps<E extends IWspUiEnv> extends WspProps<E> {
	protected _ctrlElt: HTMLDivElement;

	protected async _initialize(init: OWspPropsInit<E>) {
		const wspTitle = init.reg.env.wsp.wspTitle;
		init.disableRevert = true;
		await super._initialize(init);
		this.shadowRoot.insertBefore(<div class="question"><span id="kind"/>{`Voulez-vous vraiment supprimer l'atelier '${wspTitle}' ?`}</div>, this.shadowRoot.firstElementChild);
		this._saveBtn.label = "Supprimer";
	}

	protected initFormELt(form: HTMLFormElement): void {
		super.initFormELt(form);
		// Controles DRV / RRF
		this._ctrlElt = form.appendChild(<div class="warning">
			<span id="kind"/>
			<div class="msgs"/>
		</div>) as HTMLDivElement;
	}

	protected async updateForm(clear: boolean = true): Promise<void> {
		await super.updateForm(clear);
		let msgs = [];
		// Refresh utile de l'infoWsp car ces propriétés de sont pas dynamiquement rafraichies
		let infoWsp = await this.wsp.wspServer.config.adminWspUrl.fetchJson<JInfoWsp>(IO.qs("cdaction", "InfoWsp", "param", this.wsp.code, "wspPropsOpts", "(drvWspUsedBy 'true' listDrvs 'true' listDrfs 'true')"));

		if (infoWsp.props.drvWspUsedBy) {
			let wspListArray = infoWsp.props.drvWspUsedBy.split(" ");
			let wspList = wspListArray.join(", ");
			if (wspListArray.length == 1)
				msgs.push(`Cet atelier est source du calque de dérivation '${wspList}'. La suppression de cet atelier est donc impossible.`);
			else
				msgs.push(`Cet atelier est source des calques de dérivation : ${wspList}. La suppression de cet atelier est donc impossible.`);
			this._saveBtn.disabled = true;
		} else if (infoWsp.props.listDrvs || infoWsp.props.listDrfs) {
			if (infoWsp.props.listDrvs && this.wsp.wspServer.chain.config.drf)
				msgs.push(`Tous les ateliers 'calque de dérivation' et les ateliers 'calque de travail' liés seront également supprimés`);
			else if (infoWsp.props.listDrvs)
				msgs.push(`Tous les ateliers 'calque de dérivation' seront également supprimés`);

			if (infoWsp.props.listDrfs)
				msgs.push(`Tous les ateliers 'calque de travail', et les ateliers liés seront également supprimés`);

			this._saveBtn.label = "Supprimer les ateliers";
		}

		if (msgs.length == 0) {
			DOM.setHidden(this._ctrlElt, true);
		} else {
			let msgsElt = this._ctrlElt.querySelector(".msgs") as HTMLDivElement;
			msgs.forEach((msg) => {
				msgsElt.appendChild(<div>{msg}</div>)
			});
		}
	}

	protected async refreshBtnsState(): Promise<void> {
		this._form.checkValidity();
		this._saveBtn.disabled = false;
	}

	get areaList(): string[] {
		const wspDefProps = this.wsp.wspDefProps;
		let list: string[] = ["wspProps:remove:commons"];
		switch (this.wsp.backEnd) {
		case "fs":
			list.push("wspProps:remove:fs");
			break;
		case "odb":
			list.push("wspProps:remove:db");
			break;
		}
		if (wspDefProps && wspDefProps.drfRefWsp)
			list.push("wspProps:remove:db:drf");
		else if (wspDefProps && wspDefProps.drvAxis)
			list.push("wspProps:remove:db:drv");
		else
			list.push("wspProps:remove:db:other");
		return list;
	}

	async _doExecuteValid(): Promise<void> {
		try {
			// Fermeture de l'atelier éventuellement ouvert dans le desk
			const mainApp = (desk as AppFrameDeskFeat).getMainApp();
			if ((mainApp as IApp<IWspEnv>)?.reg?.env.wsp?.equals(this.wsp))
				if (AppFrameDeskFeat.isIn(desk)) await desk.gotoHome();

			const params = await this.getParamsToSave();
			await this.wsp.delete(params);
			return this._doClose(true);
		} catch (e) {
			await ERROR.report("Suppression de l'atelier impossible.", e);
			return this.refreshBtnsState();
		}
	}
}

