/**
 * Suppression de plusieurs wsps
 */
export class WspsRemoveProps extends WspProps<IChainEnv> {
	protected wspsReg: IReg<IWspUiEnv>[];

	protected MSG_CONFIRM = "Voulez-vous vraiment supprimer l'atelier ?";
	protected MSG_CONFIRM_MULTI = "Voulez-vous vraiment supprimer les ateliers ?";
	protected MSG_ERROR = "Suppression de(s) atelier(s) en échec.";
	protected MSG_STATUS_REMOVED = "Supprimé";
	protected MSG_BTN = "Supprimer";

	protected async _initialize(init: OWspPropsInit<IChainEnv> & IRegsPointer<IWspUiEnv>) {
		init.disableRevert = true;
		this.wspsReg = init.regs;

		await super._initialize(init);
		this.reg.installSkin("c-wsp-remove-props", this.shadowRoot);
		this._onSaveShowWaitMsg = false;
		let cplt = "";
		if (this.reg.env.universe.wspServer.chain.config.drf && this.reg.env.universe.wspServer.chain.config.drv)
			cplt = "Tous les éventuels ateliers 'calque de dérivation', et 'calque de travail' liés seront également supprimés";
		else if (this.reg.env.universe.wspServer.chain.config.drf)
			cplt = "Tous les éventuels ateliers 'calque de travail' liés seront également supprimés";
		else if (this.reg.env.universe.wspServer.chain.config.drv)
			cplt = "Tous les éventuels ateliers 'calque de dérivation' liés seront également supprimés";

		let wsps: Wsp[] = [];
		this.wspsReg.forEach((entry) => {wsps.push(entry.env.wsp)});
		WSP.sortWspsByDeep(wsps).reverse();
		let count = wsps.length;

		let msg = count > 1 ? this.MSG_CONFIRM_MULTI : this.MSG_CONFIRM;
		this.shadowRoot.insertBefore(<div>
			<div class="question">
				<span id="kind"/>
				<div>
					<div>{msg}</div>
					<div class="cplt">{cplt}</div>
				</div>
			</div>
			<WspsMonitoring î={{
				wsps: wsps,
				defaultStateKey: "toRemove",
				states: [{key: "toRemove", level: "disabled", label: "À supprimer"}]
			} as OWspsMonitoringInit}/>
		</div>, this.shadowRoot.firstElementChild);

		this._saveBtn.label = this.MSG_BTN;
	}

	async _doExecuteValid(): Promise<void> {
		let wspTitleErrosArray: string[] = [];
		let dialog: IPopupable;
		try {
			const params = await this.getParamsToSave();
			let wsps: Wsp[] = [];
			this.wspsReg.forEach((entry) => {wsps.push(entry.env.wsp)});
			WSP.sortWspsByDeep(wsps).reverse();
			let wspsMonitoring = <WspsMonitoring î={{
				wsps: wsps,
				defaultStateKey: "notStart",
				states: [
					{key: "notStart", level: "disabled", label: "En attente"},
					{key: "toRemove", level: "disabled", label: "À supprimer"},
					{key: "pending", level: "pending", label: "En cours..."},
					{key: "removed", level: "success", label: this.MSG_STATUS_REMOVED},
					{key: "failed", level: "error", label: "Échec"}
				]
			} as OWspsMonitoringInit}/> as WspsMonitoring;

			dialog = POPUP.showDialog(<div>
				<MsgLabel label="Suppression en cours..."/>
				{wspsMonitoring}
			</div>, this, {
				skinOver: "c-wsps-props-monitoraction",
				fixSize: false,
				titleBar: null,
				initWidth: '50%',
			});
			let errorsArray: any[] = [];
			for (const entry in wsps) {
				let wsp = wsps[entry];
				wspsMonitoring.updateWspStatus(wsp.code, "pending");
				try {
					await this.doDelete(wsp, params);
					wspsMonitoring.updateWspStatus(wsp.code, "removed");
					await new Promise(resolve => setTimeout(resolve, 1000));
				} catch (e) {
					wspsMonitoring.updateWspStatus(wsp.code, "failed");
					wspTitleErrosArray.push(wsp.wspTitle);
					errorsArray.push(e);
				}
			}
			await new Promise(resolve => setTimeout(resolve, 3000));
			if (errorsArray.length > 0) throw errorsArray;
			this._doClose();
		} catch (e) {
			let list = wspTitleErrosArray.join(", ");
			if (dialog) dialog.close();
			await ERROR.report(list ? this.MSG_ERROR : this.MSG_ERROR + " " + `Ateliers : '${list}'.`, e);
			await this._doClose();
		}
	}

	protected async doDelete(wsp: Wsp, params: any): Promise<void> {
		// Fermeture de l'atelier éventuellement ouvert dans le desk
		const mainApp = (desk as AppFrameDeskFeat).getMainApp();
		if ((mainApp as IApp<IWspEnv>)?.reg?.env.wsp?.equals(wsp))
			if (AppFrameDeskFeat.isIn(desk)) await desk.gotoHome();

		return wsp.delete(params);
	}

	protected async refreshBtnsState(): Promise<void> {
		this._form.checkValidity();
		this._saveBtn.disabled = false;
	}

	get areaList(): string[] {
		let list: string[] = ["wspProps:removeAll:commons"];
		switch (this.reg.env.universe.config.backEnd) {
		case "fs":
			list.push("wspProps:removeAll:fs");
			break;
		case "odb":
			list.push("wspProps:removeAll:db");
			break;
		}
		return list;
	}
}

